import React, { useEffect, useMemo } from 'react'
import { graphql, navigate } from 'gatsby'
import { useFormikContext } from 'formik';
import { FiArrowLeft } from "react-icons/fi";
import { IconButton } from '@chakra-ui/react'
import { set } from 'lodash';
import { StaticImage } from 'gatsby-plugin-image';
import AniLink from "gatsby-plugin-transition-link/AniLink"

import Box from '../components/Box'
import Text from '../components/Text'
import Flex from '../components/Flex'
import Button from '../components/Button'
import Container from '../components/Container'
import FullpageVerticalCenter from '../components/FullpageVerticalCenter'
import theme, { Media, responsive } from '../components/ThemeProvider/theme';

import Layout from '../containers/Layout'
import { trackAnswer } from '../utils/trackEvent';

const picture = {
  1: <StaticImage src="q1.png" layout="fullWidth" />,
  2: <StaticImage src="q2.png" layout="fullWidth" />,
  3: <StaticImage src="q3.png" layout="fullWidth" />,
  4: <StaticImage src="q4.png" layout="fullWidth" />,
  5: <StaticImage src="q5.png" layout="fullWidth" />,
  6: <StaticImage src="q6.png" layout="fullWidth" />,
}

const Question = ({ data, pageContext: { id, isLast, length } }) => {
  const { values, submitForm, isSubmitting } = useFormikContext()
  const answerNotMatch = useMemo(() => values.answers.length < id - 1, [id, values.answers.length])
  useEffect(() => {
    if (answerNotMatch) {
      navigate(`/q/${values.answers.length + 1}`)
    }
  }, [answerNotMatch])
  if (answerNotMatch) return null
  return (
    <Layout bg="white" footerProps={{ py: responsive('1em', 0) }} title={`第${id}題`}>
      <FullpageVerticalCenter height={`calc(100vh - ${theme.headerHeight})`}>
        <Container>
          <Flex alignItems="center" justifyContent="space-around">
            <Box width="424px" as={Media} greaterThanOrEqual="tablet">
              {picture[id]}
            </Box>
            <Box>
              <Flex
                pb="0.25em"
                alignItems="center"
                justify="space-between"
                borderColor="darkBlue"
                borderBottom="1px solid"
              >
                <IconButton
                  as={AniLink}
                  swipe
                  direction="right"
                  w="1.5em"
                  h="1.5em"
                  mr="0.25em"
                  minWidth="auto"
                  bg="transparent"
                  color="custom.darkBlue"
                  icon={<FiArrowLeft size="2em" />}
                  to={+id > 1 ? `/q/${id - 1}` : '/'}
                  isDisabled={isSubmitting}
                />
                <Text
                  fontSize={responsive('1.5em', '1.25em')}
                  letterSpacing="0.125em"
                  fontFamily="Flama"
                  color="lightGray"
                >
                  <Text.Inline color="gray">{id}</Text.Inline>/{length}
                </Text>
              </Flex>
              <Text
                mt="0.5em"
                fontWeight="500"
                color="custom.darkBlue"
                fontSize={responsive('1.125em', '1.25em')}
              >{data?.questionsJson.question}</Text>
              <Box as={Media} lessThan="tablet" pt="1em">
                {picture[id]}
              </Box>
              <Box>
                {data?.questionsJson.options.map((opt, i) => (
                  <Box mt={responsive(i && '1em' , '1.25em')} key={i}>
                    <Button
                      as={!isLast && ((p) => <AniLink top="entry" {...p} />)}
                      onClick={() => {
                        set(values, ['answers', +id - 1], i)
                        trackAnswer(id, i)
                        if(isLast) {
                          submitForm()
                        }
                      }}
                      to={!isLast && `/q/${+id + 1}`}
                      swipe
                      direction="left"
                      width="100%"
                      isDisabled={isSubmitting}
                    >
                      {opt}
                    </Button>
                  </Box>
                ))}
              </Box>
            </Box>
          </Flex>
        </Container>
      </FullpageVerticalCenter>
    </Layout>
  )
}

export default Question

export const query = graphql`
  query QuestionPageQuery($id: String) {
    questionsJson(id: {eq: $id}) {
      question
      options
    }
  }
`


